<script>
export default {
  data() {
    return {
      dateList: [],
      time_items: [],
      choose_time: "",
      time_flag: false,
      status: 1
    };
  },
  mounted() {
    let contentArr = document.querySelectorAll(
      ".ms_group .mui-control-content"
    );
    let outHeight = window.outerHeight - 80;
    contentArr[0].style.minHeight = outHeight + "px";
    contentArr[1].style.minHeight = outHeight + "px";

    let prover_ul = document.querySelector(".prover_ul");
    prover_ul.addEventListener("click", e => {
      if (!this.time_flag) {
        this.time_flag = true;
        return;
      }
      this.time_items.forEach(element => {
        element.classList.remove("time_current");
      });
      e.target.classList.add("time_current");
      this.choose_time = e.target.innerHTML;
    });

    let options = document.querySelectorAll(".diet_item");
    for (let i = 0; i < options.length; i++) {
      options[i].onclick = function() {
        if (!this.classList.contains("current_item")) {
          this.classList.add("current_item");
        } else {
          this.classList.remove("current_item");
        }
      };
    }
  },
  methods: {
    // 提交购物车 状态
    editColor(status) {
      this.status = status;
    },
    // 提交购物车
    submit_car() {
      let date = new Date();
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      var ndate = new Date(y + "-" + m + "-" + d);
      ndate.setDate(ndate.getDate() + 1);
      let ny = ndate.getFullYear();
      let nm = ndate.getMonth() + 1;
      let nd = ndate.getDate();
      this.dateList = [
        y + "-" + m + "-" + d + "  中午 10:00",
        y + "-" + m + "-" + d + "  中午 12:00",
        y + "-" + m + "-" + d + "  下午 18:00",
        y + "-" + m + "-" + d + "  晚上 21:00",
        ny + "-" + nm + "-" + nd + "  中午 10:00"
      ];

      this.$nextTick(() => {
        let time_prover = document.querySelector("#time_prover");
        this.time_items = document.querySelectorAll(".prover_ul li");
        time_prover.style.display = "block";
        document.body.classList.add("backdrop");
      });
    },
    // 关闭 时间选择
    close_prover() {
      document.querySelector("#time_prover").style.display = "none";
      if (this.choose_time == "") {
        this.mui.alert("没有选择");
      } else {
        let choose_diet = document.querySelector("#choose_diet");
        this.mui.alert("选择了时间:" + this.choose_time, () => {
          choose_diet.style.display = "block";
          console.log(choose_diet.style);
        });
      }
      this.time_flag = false;
    },
    pay(method_index) {
      let choose_diet = (document.querySelector("#choose_diet").style.display =
        "none");
    }
  }
};
</script>

