import User from "@/components/user.vue"; //个人中心
import JuCan from "@/components/userComponents/jucan.vue"; //个人中心聚餐圈
import complatedOrder from "@/components/userComponents/complatedOrder.vue"; //聚餐圈已完成
import IOrder from "@/components/userComponents/orderInfo.vue"; //聚餐圈的我发起
import DinnerDetail from "@/components/userComponents/dinnerDetail.vue"; //聚餐圈里我发起的聚餐详情
import UserWallet from "@/components/userComponents/userwallet.vue"; //个人中心 我的钱包
import MyYuDing from "@/components/userComponents/my_yuding.vue"; //个人中心 我的预定
import YDOrdered from "@/components/userComponents/yd_ordered.vue"; //个人中心 我的预定->已点菜
import CompleteReserve from "@/components/userComponents/complete_reserve.vue"; //个人中心 我的预定->已完成预定
import MyOrder from "@/components/userComponents/my_order.vue"; //个人中心 我的订单
import OrderDetail from "@/components/userComponents/order_detail.vue"; //个人中心 订单详情
import Coupon from "@/components/userComponents/coupon.vue"; //个人中心 优惠券

export default {
  components: {
    shoppingCart,
    PayOrder,
    YuDing,
    PaiHao,
    chooseTab,
    inviteFriends,
    User,
    JuCan,
    complatedOrder,
    IOrder,
    DinnerDetail,
    PaySuccess,
    UserWallet,
    MyYuDing,
    YDOrdered,
    CompleteReserve,
    MyOrder,
    OrderDetail,
    Coupon
  },
  data() {
    return {
      // 用来控制标题栏
      flag: false,
      // 标题
      title: ""
    };
  },
  methods: {
    // tab切换
    tabSwitch(e, index) {
      index !== 1 ? (this.flag = true) : (this.flag = false);
      switch (index) {
        case 2:
          this.title = "堂食预定";
          break;
        case 3:
          this.title = "购物车";
          break;
        case 4:
          this.title = "个人中心";
          break;
      }
      document.querySelector(".mui-content").style.transform =
        "translate3d(0px, 0px, 0px) translateZ(0px)";
    }
  },
  mounted() {
    this.mui.init();
    this.mui.init({
      swipeBack: false
    });
    //初始化单页view
    var viewApi = mui("#app").view({
      defaultPage: "#setting"
    });
    (function($) {
      //处理view的后退与webview后退
      var oldBack = $.back;
      $.back = function() {
        if (viewApi.canBack()) {
          //如果view可以后退，则执行view的后退
          viewApi.back();
        } else {
          //执行webview后退
          oldBack();
        }
      };

      $(".mui-scroll-wrapper").scroll({
        indicators: true //是否显示滚动条
      });
      // 获取标题栏
    })(this.mui);
  }
};
</script>

